// 
import setting from '@/setting.js'

export default {
  namespaced: true,
  state: {
    // 
    info: setting.user.info
  },
  mutations: {
    /**
     * @description 
     * @param {Object} state vuex state
     * @param {*} info info
     */
    set (state, info) {
      // store 
      state.info = info
      // 
      this.dispatch('d2admin/db/set', {
        dbName: 'sys',
        path: 'user.info',
        value: info,
        user: true
      })
    },
    /**
     * @description 
     * @param {Object} state vuex state
     */
    async load (state) {
      // store 
      state.info = await this.dispatch('d2admin/db/get', {
        dbName: 'sys',
        path: 'user.info',
        defaultValue: setting.user.info,
        user: true
      })
    }
  }
}
