// 
import setting from '@/setting.js'

export default {
  namespaced: true,
  state: {
    //  tab 
    pool: [],
    // 
    opened: setting.page.opened,
    // 
    current: ''
  },
  getters: {
    /**
     * @description  name
     * @param {*} state vuex state
     */
    keepAlive (state) {
      return state.opened.filter(item => {
        if (item.meta) {
          if (item.meta.notCache) {
            return false
          }
        }
        return true
      }).map(e => e.name)
    }
  },
  mutations: {
    /**
     * @class current
     * @description 
     * @param {Object} state vuex state
     * @param {Object} param { name, params, query } 
     */
    open (state, { name, params, query }) {
      // 
      let opened = state.opened
      //  
      let pageOpendIndex = 0
      const pageOpend = opened.find((page, index) => {
        const same = page.name === name
        pageOpendIndex = same ? index : pageOpendIndex
        return same
      })
      if (pageOpend) {
        //   name 
        this.commit('d2admin/page/openedUpdate', {
          index: pageOpendIndex,
          params,
          query
        })
      } else {
        // 
        let page = state.pool.find(t => t.name === name)
        if (page) {
          this.commit('d2admin/page/add', {
            tag: page,
            params,
            query
          })
        }
      }
      this.commit('d2admin/page/currentSet', name)
    },
    /**
     * @class current
     * @description  name
     * @param {Object} state vuex state
     * @param {String} name new name
     */
    currentSet (state, name) {
      state.current = name
    },
    /**
     * @class opened
     * @description 
     * @param {Object} state vuex state
     * @param {Object} param { index, params, query } 
     */
    openedUpdate (state, { index, params, query }) {
      // 
      let page = state.opened[index]
      page.params = params || page.params
      page.query = query || page.query
      state.opened.splice(index, 1, page)
      // 
      this.commit('d2admin/page/opend2db')
    },
    /**
     *  opened   state.opened
     * @param {Object} state vuex state
     */
    opend2db (state) {
      this.dispatch('d2admin/db/set', {
        dbName: 'sys',
        path: 'page.opened',
        value: state.opened,
        user: true
      })
    },
    /**
     * @class opened
     * @description 
     * @param {Object} state vuex state
     */
    async openedLoad (state) {
      // store 
      const value = await this.dispatch('d2admin/db/get', {
        dbName: 'sys',
        path: 'page.opened',
        defaultValue: setting.page.opened,
        user: true
      })
      //  
      //  name 
      // 
      // valid  1, 1, 0, 1 => , , , 
      const valid = []
      // 
      state.opened = value.map(opened => {
        // 
        if (opened.name === 'index') {
          valid.push(1)
          return opened
        }
        //  name 
        const find = state.pool.find(item => item.name === opened.name)
        // 
        valid.push(find ? 1 : 0)
        //  
        //  params  query, 
        return Object.assign({}, opened, find)
      }).filter((opened, index) => valid[index] === 1)
    },
    /**
     * @class opened
     * @description  tag ()
     * @param {Object} state vuex state
     * @param {Object} param new tag info
     */
    add (state, { tag, params, query }) {
      //  tag 
      let newTag = tag
      newTag.params = params || newTag.params
      newTag.query = query || newTag.query
      // 
      state.opened.push(newTag)
      // 
      this.commit('d2admin/page/opend2db')
    },
    /**
     * @class opened
     * @description  tag ()
     * @param {Object} state vuex state
     * @param {Object} param { tagName: , vm: vue }
     */
    close (state, { tagName, vm }) {
      // 
      let newPage = state.opened[0]
      const isCurrent = state.current === tagName
      // 
      if (isCurrent) {
        // 
        let len = state.opened.length
        for (let i = 1; i < len; i++) {
          if (state.opened[i].name === tagName) {
            if (i < len - 1) {
              newPage = state.opened[i + 1]
            } else {
              newPage = state.opened[i - 1]
            }
            break
          }
        }
      }
      // 
      const index = state.opened.findIndex(page => page.name === tagName)
      if (index >= 0) {
        state.opened.splice(index, 1)
      }
      // 
      this.commit('d2admin/page/opend2db')
      // 
      if (isCurrent) {
        const { name = '', params = {}, query = {} } = newPage
        let routerObj = {
          name,
          params,
          query
        }
        vm.$router.push(routerObj)
      }
    },
    /**
     * @class opened
     * @description 
     * @param {Object} state vuex state
     * @param {Object} param { pageSelect: tagName, vm: vue }
     */
    closeLeft (state, { pageSelect, vm } = {}) {
      const pageAim = pageSelect || state.current
      let currentIndex = 0
      state.opened.forEach((page, index) => {
        if (page.name === pageAim) {
          currentIndex = index
        }
      })
      if (currentIndex > 0) {
        state.opened.splice(1, currentIndex - 1)
      }
      state.current = pageAim
      if (vm && vm.$route.name !== pageAim) {
        vm.$router.push({
          name: pageAim
        })
      }
      // 
      this.commit('d2admin/page/opend2db')
    },
    /**
     * @class opened
     * @description 
     * @param {Object} state vuex state
     * @param {Object} param { pageSelect: tagName, vm: vue }
     */
    closeRight (state, { pageSelect, vm } = {}) {
      const pageAim = pageSelect || state.current
      let currentIndex = 0
      state.opened.forEach((page, index) => {
        if (page.name === pageAim) {
          currentIndex = index
        }
      })
      state.opened.splice(currentIndex + 1)
      state.current = pageAim
      if (vm && vm.$route.name !== pageAim) {
        vm.$router.push({
          name: pageAim
        })
      }
      // 
      this.commit('d2admin/page/opend2db')
    },
    /**
     * @class opened
     * @description  tag
     * @param {Object} state vuex state
     * @param {Object} param { pageSelect: tagName, vm: vue }
     */
    closeOther (state, { pageSelect, vm } = {}) {
      const pageAim = pageSelect || state.current
      let currentIndex = 0
      state.opened.forEach((page, index) => {
        if (page.name === pageAim) {
          currentIndex = index
        }
      })
      if (currentIndex === 0) {
        state.opened.splice(1)
      } else {
        state.opened.splice(currentIndex + 1)
        state.opened.splice(1, currentIndex - 1)
      }
      state.current = pageAim
      if (vm && vm.$route.name !== pageAim) {
        vm.$router.push({
          name: pageAim
        })
      }
      // 
      this.commit('d2admin/page/opend2db')
    },
    /**
     * @class opened
     * @description  tag
     * @param {Object} state vuex state
     * @param {Object} vm vue
     */
    closeAll (state, vm) {
      state.opened.splice(1)
      // 
      this.commit('d2admin/page/opend2db')
      // 
      if (vm.$route.name !== 'index') {
        vm.$router.push({
          name: 'index'
        })
      }
    },
    /**
     * @class pool
     * @description  pool ()
     * @param {Object} state vuex state
     * @param {Array} routes routes
     */
    init (state, routes) {
      const pool = []
      const push = function (routes) {
        routes.forEach(route => {
          if (route.children) {
            push(route.children)
          } else {
            const { meta, name, path } = route
            pool.push({ meta, name, path })
          }
        })
      }
      push(routes)
      state.pool = pool
    }
  }
}
